import React from 'react';
import './App.css';
import { Bacon } from './components/Bacon';
import { Hipsters } from './components/Hipsters';
import { Zombies } from './components/Zombies';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
} from 'react-router-dom';
function App() {
  return (
    <Router>
      <div className="wrapper">
        <nav id="side-nav" aria-labelledby="menu-label">
          <span id="side-nav-label">Menu</span>
          <Link to="/zombies">Zombies</Link>
          <Link to="/hipsters">Hipsters</Link>
          <Link to="/bacon">Bacon</Link>
        </nav>
        <main>
          <Switch>
            <Route exact path="/">
              <Redirect to="/zombies" />
            </Route>
            <Route path="/zombies">
              <Zombies />
            </Route>
            <Route path="/hipsters">
              <Hipsters />
            </Route>
            <Route path="/bacon">
              <Bacon />
            </Route>
          </Switch>
        </main>
      </div>
    </Router>
  );
}

export default App;
